.footerContainer {
  width: 100%;
  height: 15vh;
  background: #162C4E;
  position: fixed;
  left: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;

  .footer {
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    @media screen and (max-width: 900px) {
      height: 70%;
      margin: auto;
      display: grid;
      justify-items: center;
      align-items: center;
    }

    form {
      box-sizing: border-box;

      position: relative;
      width: 440px;
      height: 59px;

      background: #FFFFFF;
      border: 1px solid rgba(0, 0, 0, 0.8);
      box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.4);
      border-radius: 40px;

      @media screen and(max-width:768px) {
        width: 360px;
        height: 41px;
      }

      @media screen and(max-width:380px) {
        width: 280px;
        height: 41px;
      }

      .form-input {
        border: none;
        outline: none;

        position: absolute;
        width: 259px;
        height: 27px;
        left: 27px;
        top: 16px;

        font-family: 'Roboto';
        font-style: normal;
        font-weight: 400;
        font-size: 18px;
        line-height: 150%;
        /* identical to box height, or 27px */

        color: rgba(0, 0, 0, 0.6);

        @media screen and(max-width:768px) {
          width: 202px;
          height: 21px;
          left: 20px;
          top: 10px;

          font-size: 14px;
        }
      }

      .form-button {
        cursor: pointer;
        position: absolute;
        width: 43px;
        height: 43px;
        left: 389px;
        top: 8px;

        border: none;
        border-radius: 50%;

        background: #DF2224;

        img {
          transform: translateY(2px);
        }

        @media screen and(max-width:768px) {
          width: 33px;
          height: 33px;
          left: 322px;
          top: 3px;
        }

        @media screen and(max-width:380px) {
          width: 33px;
          height: 33px;
          left: 242px;
          top: 3px;
        }

      }

      .loader {
        width: 43px;
        height: 43px;

        position: absolute;
        left: 389px;
        top: 8px;

        border: 4px solid #DF2224;
        border-top: 4px solid #162C4E;
        border-radius: 50%;

        animation: spin 1s linear infinite;

        box-sizing: border-box;


        @keyframes spin {
          0% {
            transform: rotate(0deg);
          }

          100% {
            transform: rotate(360deg);
          }
        }

        @media screen and(max-width:768px) {
          width: 33px;
          height: 33px;
          left: 322px;
          top: 3px;
        }

        @media screen and(max-width:380px) {
          width: 33px;
          height: 33px;
          left: 242px;
          top: 3px;
        }
      }
    }

    .otherEventsButt {
      position: absolute;
      right: 5%;

      cursor: pointer;

      width: 180px;
      height: 30px;
      border: none;
      background: #162C4E;

      font-family: 'Poppins';
      font-style: normal;
      font-weight: 400;
      font-size: 20px;
      line-height: 150%;
      color: #FFFFFF;

      img {
        transform: translateY(5px);
      }

      @media screen and (max-width: 900px) {
        position: static;
        font-size: 16px;
      }
    }
  }
}